.tabs-layout .lanif-tabs-content {
  background: #fff;
}
.tabs-layout .lanif-tabs-content > .ant-tabs-bar {
  margin-bottom: 0;
  padding: 4px 8px;
}
.tabs-layout .lanif-tabs-content > .ant-tabs-bar .ant-tabs-nav-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.tabs-layout .lanif-tabs-content > .ant-tabs-bar .ant-tabs-tab {
  border: 1px solid #e8e8e8;
  border-radius: 4px;
  line-height: 32px;
}
.tabs-layout .lanif-tabs-content > .ant-tabs-bar .ant-tabs-tab .tab-title {
  display: inline-block;
}
.tabs-layout .lanif-tabs-content > .ant-tabs-bar .ant-tabs-tab .tab-title::before {
  content: '';
  display: inline-block;
  position: relative;
  width: 8px;
  height: 8px;
  background: #ddd;
  margin-top: 15px;
  margin-right: 8px;
  border-radius: 8px;
  -webkit-transition: .3s;
  transition: .3s;
}
.tabs-layout .lanif-tabs-content > .ant-tabs-bar .ant-tabs-tab.ant-tabs-tab-active .tab-title::before {
  background: #1890ff;
}
.tabs-layout .lanif-tabs-content > .ant-tabs-bar .ant-tabs-ink-bar {
  visibility: visible;
  height: 3px;
  border-radius: 0 0 4px 4px;
}
.tabs-layout .lanif-tabs-content > .ant-tabs-content {
  position: relative;
  height: calc(100% - 44px);
  overflow: auto;
}
.basic-layout.fixed .tabs-layout .lanif-tabs-content {
  height: 100%;
  width: 100%;
  position: absolute;
}
